<template>
	<div id="app">
		<router-view />
	</div>
</template>
<script>
require('./untils/MD5')
export default {
	data () {
		return {
		};
	},
	mounted () {
		this.loadIfram();
		try {
			window.addEventListener("message", event => {
			let data = event.data
			console.log('event', event);
			if (data.cmd == "phone") {
				let tel = data.params.data
				console.log('tel', tel.MD5());
				window.sessionStorage.setItem('tel',tel.MD5())
			} else {
				window.sessionStorage.setItem('tel',"none")
				// window.sessionStorage.setItem('tel','error')
			}
		})
		} catch (error) {
			console.log('error',error);
		}
		
	},
	methods: {
		loadIfram () {
			var iframe = null
			try {
				iframe = document.createElement('<iframe style="display:none" id="_tj_" name="_tj"></iframe>')
			} catch (e) {
				iframe = document.createElement('iframe')
				iframe.name = '_ifr'
				iframe.id = '_ifr_'
				iframe.style = "display:none"
			}
			iframe.src = this.VUE_ANALYSIS_API + "?requestid=authent"
			document.body.appendChild(iframe)
		},
	}
};
</script>
<style>
body {
	font-size: 16px;
	background-color: #fff;
	-webkit-font-smoothing: antialiased;
}
</style>
